@import "/src/styles/transitions";
.container {
    display: flex;
    flex-shrink: 0;
    flex-grow: 0;
    align-items: flex-start;
    width: 19px;
    height: 14px;
    appearance: none;
    background: none;
    border: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    box-shadow: none;
    position: relative;
    &:focus {
        outline: none;
    }
}

.containerOpen {}

.dash {
    display: inline-block;
    position: absolute;
    width: 100%;
    height: 2px;
    border-radius: 2px;
    background: currentColor;
    left: 0;
    transition: all 0.25s ease-in;
    &:nth-child(1) {
        top: 0;
        .containerOpen & {
            transform: rotate(-45deg);
            top: 7px;
        }
    }
    &:nth-child(2) {
        top: 6px;
        .containerOpen & {
            transform: scaleX(0);
        }
    }
    &:nth-child(3) {
        bottom: 0;
        .containerOpen & {
            transform: rotate(45deg);
            bottom: 5px;
        }
    }
}