@import "/src/styles/variables/common";
@import "/src/styles/mixins/breakpoints";
@import "/src/styles/common";
@import "/src/styles/content";
@import "/src/styles/transitions";
.container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    padding: 12rem 0 12rem;
    box-sizing: border-box;
    z-index: 999;
    background: #2C2C52;
    transition: background-color 0.15s ease-in;
    @include breakpoint("sm-max") {
        padding: 15.3rem 0 15rem;
    }
}

.containerScrolled {
    background: $bg-light;
    &.poolsPage {
        background: transparent;
    }
}

.content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    div a:first-child {
        float: left;
    }
    div a:nth-child(2) {
        float: right;
    }
    div {
        display: flex;
        align-items: center;
    }
}

.logo {
    width: 100%;
    height: 100%;
}

.logoLink {
    margin: 0;
    padding: 0;
    width: 70rem;
    height: 70rem;
    @include breakpoint("md-max") {
        width: 50rem;
        height: 50rem;
    }
    .poolsPage & {
        visibility: hidden;
    }
}

.nav {
    margin: 0 20rem;
}

