@import "/src/styles/variables/common";
@import "/src/styles/mixins/breakpoints";
@import "/src/styles/animations";
.container {}

.title {
    font-family: $font-primary;
    font-size: 35px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 35px;
    letter-spacing: normal;
    text-align: left;
    color: $color-thin-darker;
    margin: 0 0 20px;
    .containerLoading & {
        background: $bg-medium;
        color: transparent;
        height: 40px;
        margin: 10px 0 30px;
        width: 250px;
        border-radius: 15.5px;
        animation: $opacity-pulse;
    }
}

.items {
    list-style: none;
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(340px, 380px));
    grid-gap: 40rem;
    justify-content: center;
    ;
    @include breakpoint(sm-max) {
        grid-template-columns: 1fr;
        grid-gap: 24rem;
    }
}