@import "/src/styles/mixins/breakpoints";
@import "/src/styles/variables/common";
.container {
    border: 4px solid white;
    position: relative;
    display: block;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background: $bg-light no-repeat center / cover;
    // background-origin: content-box;
    // padding: 8px;
    @include breakpoint("xl-min") {
        width: 60px;
        height: 60px;
    }
    box-shadow: 3px 3px 32px 0 rgba(35, 37, 75, 0.18);
}

.icon {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 15px;
    height: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: $bg-medium no-repeat center / cover;
    border-radius: 50%;
    border: none;
    @include breakpoint("xl-min") {
        width: 20px;
        height: 20px;
    }
}

.iconImage {
    max-height: 18px;
    max-width: 18px;
}