@import "/src/styles/variables/common";
@import "/src/styles/mixins/breakpoints";
.container {
    padding: 10vh 12vw;
    box-sizing: border-box;
    h1 {
        color: #1536ff;
        font-weight: bold;
        letter-spacing: 1px;
        @include breakpoint("sm-max") {
            font-size: 24px;
        }
    }
    p {
        @include breakpoint("sm-max") {
            font-size: 12px;
        }
    }
    @include breakpoint("md-max") {
        text-align: center;
    }
    @include breakpoint("sm-max") {
        text-align: center;
        padding: 3vh 12vw;
    }
}

.rowFlex {
    display: flex;
    align-items: center;
}

.bitCoinDiv {
    @include breakpoint("md-max") {
        display: none;
    }
}

.rowSecond {
    padding-top: 5vh;
    div {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
}

.btnBlue {
    background-color: #1536ff;
    color: white;
}

.image {
    width: 210px;
    height: 210px;
    float: right;
    margin-right: 3vw;
    @include breakpoint("md-max") {
        display: none;
    }
    @include breakpoint("sm-max") {
        display: none;
    }
}

.colFirst {
    float: right;
    width: 44%;
    @include breakpoint("sm-max") {
        // width: 100%;
        // height: 120px;
    }
}

.colSecond {
    float: left;
    width: 52.5%;
    @include breakpoint("sm-max") {
        // width: 100%;
        // height: 120px;
    }
}

.colBelow {
    float: left;
    width: 100%;
}

.imageLeft {
    float: right;
    margin: 10px;
    width: 100%;
    max-height: auto;
    box-shadow: 1.5px 1.5px 10px 0 rgba(200, 200, 200, 50%);
    @include breakpoint("sm-max") {
        // height: 130px;
        margin: 0px 10px;
    }
}

.imageRight {
    margin: 10px;
    width: 100%;
    height: auto;
    box-shadow: 1.5px 1.5px 10px 0 rgba(200, 200, 200, 50%);
    @include breakpoint("sm-max") {
        // height: 130px;
        margin: 0px 10px;
    }
}

.imageBelow {
    margin: -10px 10px;
    width: 100%;
    height: auto;
    box-shadow: 1.5px 1.5px 10px 0 rgba(200, 200, 200, 50%);
    @include breakpoint("sm-max") {
        // height: 120px;
    }
}