@import "/src/styles/variables/common";
@import "/src/styles/mixins/breakpoints";
@font-face {
    font-family: "Barbaro";
    src: local("Barbaro"), url("../../assets/fonts/barbaro.ttf") format("truetype");
    font-weight: normal;
}

@font-face {
    font-family: "Anita semi square";
    src: local("Anita semi square"), url("../../assets/fonts/Anita semi square.ttf") format("truetype");
    font-weight: normal;
}

@font-face {
    font-family: "barbaro punta";
    src: local("barbaro punta"), url("../../assets/fonts/barbaro punta.ttf") format("truetype");
    font-weight: normal;
}

@font-face {
    font-family: "EmeraldGrey-DxXW";
    src: local("EmeraldGrey-DxXW"), url("../../assets/fonts/EmeraldGrey-DxXW.ttf") format("truetype");
    font-weight: normal;
}

@font-face {
    font-family: "LeanForeword-MR0x";
    src: local("LeanForeword-MR0x"), url("../../assets/fonts/LeanForeword-MR0x.ttf") format("truetype");
    font-weight: normal;
}

@font-face {
    font-family: "Miamagon-5YBx";
    src: local("Miamagon-5YBx"), url("../../assets/fonts/Miamagon-5YBx.ttf") format("truetype");
    font-weight: normal;
}

@font-face {
    font-family: "Vintage Legends FREE";
    src: local("Vintage Legends FREE"), url("../../assets/fonts/Vintage Legends FREE.otf") format("truetype");
    font-weight: normal;
}

.container {
    margin-top: 3em;
    min-height: 220px;
    padding: 70px 100px 30px 100px;
    background: linear-gradient(to bottom, rgba(117, 231, 136, 0.3) rgba(7, 238, 46, 0.3));
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    flex-direction: column;
    @include breakpoint("md-max") {
        padding: 30px 50px;
    }
    @include breakpoint("sm-max") {
        padding: 20px;
        background-image: linear-gradient(to bottom, rgba(117, 231, 136, 0.3) rgba(7, 238, 46, 0.3)) !important;
        min-height: 120px;
        justify-content: center;
        opacity: 0.5;
    }
    @include breakpoint("xl-min") {
        min-height: 200px;
    }
}

.image {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    // right: 7vw;
    right: 0vw;
    // max-width: 210px;
    max-width: 310px;
    z-index: 0;
    @include breakpoint("md-max") {
        right: -16px;
    }
    @include breakpoint("sm-max") {
        display: none;
    }
}

.content {
    position: relative;
    z-index: 1;
}

.title {
    font-family: $font-primary;
    font-size: 38rem;
    font-weight: bolder;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: 1px;
    text-align: center;
    margin: 0;
    color: #2C2C52;
    // color: rgb(241, 175, 31);
    @include breakpoint("sm-max") {
        text-align: center;
    }
}

.text {
    // font-family: $font-primary;
    font-size: 16rem;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: -0.5px;
    text-align: center;
    margin: 5px 0 0;
    // font-family: Barbaro;
    // font-family: Anita semi square;
    // font-family: Barbaro punta;
    // font-family: EmeraldGrey-DxXW;
    // font-family: LeanForeword-MR0x;
    // font-family: Miamagon-5YBx;
    // font-family: Vintage Legends FREE;
    // color: #f53d18;
    color: #25294d;
    @include breakpoint("sm-max") {
        text-align: center;
    }
}

.learnMore {
    font-size: 13rem;
    text-decoration: underline;
    font-stretch: normal;
    font-weight: bold;
    line-height: 1.2;
    letter-spacing: -0.5px;
    text-align: center;
    margin-left: 0.3em;
    // font-family: Barbaro;
    // font-family: Anita semi square;
    // font-family: Barbaro punta;
    // font-family: EmeraldGrey-DxXW;
    // font-family: LeanForeword-MR0x;
    // font-family: Miamagon-5YBx;
    // font-family: Vintage Legends FREE;
    // color: #f53d18;
    color: #2C2C52;
    @include breakpoint("sm-max") {
        // text-align: center;
    }
}