@use "sass:math";
$breakpoints-min: ( "sm-min": 768px, "md-min": 1024px, "lg-min": 1280px, "xl-min": 2000px);
$breakpoints-max: ( "sm-max": 767px, "md-max": 1023px, "lg-max": 1179px, "xl-max": 1999px);
@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@700&display=swap');
* {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: poppins;
}

html {
    overflow: hidden;
    overflow-y: auto;
    font-family: $font-primary;
    // Keep this for proper scaling using relative units (rem);
    // ---------------------------------------------------------------------
    font-size: 1px;
    @include breakpoint("lg-min") {
        font-size: (1/1280) * 100vw;
    }
    @include breakpoint("md-max") {
        font-size: (1/1024) * 100vw;
    }
    @include breakpoint("sm-max") {
        font-size: 1px;
    }
    // Enable this to support layout proportional scaling on mobile
    //@include breakpoint("sm-max") {
    //  font-size: (1/375) * 100vw;
    //}
    // ----------------------------------------------------------------------
}

body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    overflow-y: auto;
    // background: url(/assets/img/background.jfif) no-repeat;
    background-color: #ebeff7;
    background-size: cover;
    // background-position-y: 5em;
}

input[type=text],
textarea,
button {
    outline: none;
}

input[type=text]:focus,
textarea:focus,
button:focus,
a:focus {
    outline: none;
}