@-webkit-keyframes fadeIn {
  0% { opacity:0; }
  100% { opacity:1; }
}

@keyframes fadeIn {
  0% { opacity:0; }
  100% { opacity:1; }
}

@-webkit-keyframes riseUp {
  from { top: 30px; }
  to { top: 0px; }
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes riseUp {
  from { top: 30px; }
  to { top: 0px; }
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@-webkit-keyframes riseLeft {
  from { left: 100px; }
  to { left: 0px; }
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes riseLeft {
  from { left: 100px; }
  to { left: 0px; }
  0% { opacity: 0; }
  100% { opacity: 1; }
}
@-webkit-keyframes riseRight {
  from { right: 100px; }
