  0% { width: 100%; }
  100% { width: 0%; }
}

@keyframes scaleX {
  0% { width: 100%; }
  100% { width: 0%; }
}

@-webkit-keyframes rotatation {
  from { -webkit-transform: rotate(90deg); transform: rotate(90deg); }
