blockquote {
  border-radius: 3px;
  position: relative;  /*  <--- */
  text-align: center;
  padding: 1rem 1.2rem;
  width: 80%;  /* create space for the quotes */
  margin: 1rem auto 2rem;
  font-size: 25px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.56;
  letter-spacing: normal;
  color: #535564;
  max-width: 1200px;
  -webkit-margin-before: 5px;
          margin-block-start: 5px;
}
blockquote:before,
blockquote:after{
  font-family: FontAwesome;
  position: absolute;
  /* -- inside the relative position of blockquote -- */
  top: 13px;
  color: #E8E8E8;
  font-size: 20px;
}
blockquote:before{
  content: "\f10d";
  margin-top: 10px;
  right: 97%;
  color: #cbcbcb;
}
blockquote:after{
  content: "\f10e";
  margin-bottom: 5px;
  top: auto;
  bottom: 13px;
  padding-left: 10px;
}

