@import "/src/styles/variables/common";
@import "/src/styles/mixins/breakpoints";
@import "/src/styles/animations";
.manualButton {
    height: 28px;
    // border: 1px solid #e8eaeb;
    border: none;
    border-radius: 7.5px;
    // padding: 0 10px !important;
    color: #737699;
    font-size: 13px;
    font-weight: bold;
    @include breakpoint("xl-min") {
        height: 33px;
        padding: 0 15px !important;
        font-size: 13px;
    }
    .comingSoon & {
        color: #ebebeb;
    }
}

.manualButtonIcon {
    width: 11px;
    height: 13.5px;
    margin: 0 7px;
    object-fit: contain;
    transform: rotateY(180deg) rotate(284deg);
    path {
        fill: #737699;
    }
    .comingSoon & {
        path {
            fill: #ebebeb;
        }
    }
}

.footer {
    padding: 16px 30px !important;
    margin-top: 0;
    border: none !important;
}

.detailsButton {
    opacity: 1;
    font-size: 13px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 2;
    letter-spacing: normal;
    text-align: right;
    color: #737699;
    @include breakpoint("xl-min") {
        font-size: 14px;
    }
    &:disabled {
        background: none;
        color: #ebebeb;
    }
}

.detailsButtonIcon {
    width: 12px;
    font-weight: 100;
    height: auto;
    .detailsButtonExpanded & {
        transform: rotateX(180deg);
    }
    .comingSoon & {
        path {
            fill: #ebebeb;
        }
    }
}

.expanded {
    margin-top: 20px;
    @include breakpoint("xl-min") {
        * {
            font-size: 14px !important;
        }
    }
}

.actions {
    display: flex;
    align-items: center;
    justify-content: end;
}